
const BTokens = ({b0, b1, b2}) => {
    return (
        <div>
        <h3>BToken Info</h3>
        <table>
        <tbody>
            <tr>
                <td>symbol</td>
                <th>{b0.symbol}</th>
                <th>{b1.symbol}</th>
                <th>{b2.symbol}</th>
            </tr>
            <tr>
                <td>price</td>
                <td>{b0.price}</td>
                <td>{b1.price}</td>
                <td>{b2.price}</td>
            </tr>
            <tr>
                <td>liquidity</td>
                <td>{b0.liquidity}</td>
                <td>{b1.liquidity}</td>
                <td>{b2.liquidity}</td>
            </tr>
            <tr>
                <td>pnl</td>
                <td>{b0.pnl}</td>
                <td>{b1.pnl}</td>
                <td>{b2.pnl}</td>
            </tr>
            <tr>
                <td>cumulativePnl</td>
                <td>{b0.cumulativePnl}</td>
                <td>{b1.cumulativePnl}</td>
                <td>{b2.cumulativePnl}</td>
            </tr>
            <tr>
                <td>user_liquidity</td>
                <td>{b0.user_liquidity}</td>
                <td>{b1.user_liquidity}</td>
                <td>{b2.user_liquidity}</td>
            </tr>
            <tr>
                <td>user_pnl</td>
                <td>{b0.user_pnl}</td>
                <td>{b1.user_pnl}</td>
                <td>{b2.user_pnl}</td>
            </tr>
            <tr>
                <td>user_lastCumulativePnl</td>
                <td>{b0.user_lastCumulativePnl}</td>
                <td>{b1.user_lastCumulativePnl}</td>
                <td>{b2.user_lastCumulativePnl}</td>
            </tr>
            <tr>
                <td>user_margin</td>
                <td>{b0.user_margin}</td>
                <td>{b1.user_margin}</td>
                <td>{b2.user_margin}</td>
            </tr>
        </tbody>
        </table>
        </div>
    )
}

export default BTokens
