import {useState} from 'react'

const Pool = ({poolinfo, b0, b1, b2, onMint, onApprove, onAddLiquidity, onRemoveLiquidity, onAddMargin, onRemoveMargin}) => {
    const [vusdt, setVUsdt] = useState(0)
    const [vweth, setVWeth] = useState(0)
    const [vsusi, setVSusi] = useState(0)
    return (
        <div>
            <h3>Pool Info</h3>
            <table>
            <tbody>
                <tr>
                    <th></th>
                    <th>USDT</th>
                    <th>WETH</th>
                    <th>SUSHI</th>
                    <th>Protocol Fee Accrued</th>
                </tr>
                <tr>
                    <th>Pool</th>
                    <td>{poolinfo.balanceUSDT}</td>
                    <td>{poolinfo.balanceWETH}</td>
                    <td>{poolinfo.balanceSUSI}</td>
                    <td>{poolinfo.protocolFeeAccrued}</td>
                </tr>
                <tr>
                    <th>User</th>
                    <td>{b0.user_balance}</td>
                    <td>{b1.user_balance}</td>
                    <td>{b2.user_balance}</td>
                    <td></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onMint(0)}>Mint USDT</button></td>
                    <td><button onClick={() => onMint(1)}>Mint WETH</button></td>
                    <td><button onClick={() => onMint(2)}>Mint SUSHI</button></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onApprove(0)}>Approve USDT</button></td>
                    <td><button onClick={() => onApprove(1)}>Approve WETH</button></td>
                    <td><button onClick={() => onApprove(2)}>Approve SUSHI</button></td>
                </tr>
                <tr>
                    <td/>
                    <td><input type='text' value={vusdt} onChange={(e) => setVUsdt(e.target.value)} /></td>
                    <td><input type='text' value={vweth} onChange={(e) => setVWeth(e.target.value)} /></td>
                    <td><input type='text' value={vsusi} onChange={(e) => setVSusi(e.target.value)} /></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onAddLiquidity(0, vusdt)}>AddLiquidity USDT</button></td>
                    <td><button onClick={() => onAddLiquidity(1, vweth)}>AddLiquidity WETH</button></td>
                    <td><button onClick={() => onAddLiquidity(2, vsusi)}>AddLiquidity SUSHI</button></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onRemoveLiquidity(0, vusdt)}>RemoveLiquidity USDT</button></td>
                    <td><button onClick={() => onRemoveLiquidity(1, vweth)}>RemoveLiquidity WETH</button></td>
                    <td><button onClick={() => onRemoveLiquidity(2, vsusi)}>RemoveLiquidity SUSHI</button></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onAddMargin(0, vusdt)}>AddMargin USDT</button></td>
                    <td><button onClick={() => onAddMargin(1, vweth)}>AddMargin WETH</button></td>
                    <td><button onClick={() => onAddMargin(2, vsusi)}>AddMargin SUSHI</button></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onRemoveMargin(0, vusdt)}>RemoveMargin USDT</button></td>
                    <td><button onClick={() => onRemoveMargin(1, vweth)}>RemoveMargin WETH</button></td>
                    <td><button onClick={() => onRemoveMargin(2, vsusi)}>RemoveMargin SUSHI</button></td>
                </tr>
            </tbody>
            </table>
        </div>
    )
}

export default Pool
