import {useState, useEffect} from 'react'
import Web3 from 'web3'
import abis from './utils/abis'

import Pool from './components/Pool'
import Symbols from './components/Symbols'
import BTokens from './components/BTokens'
import Liquidation from './components/Liquidation'

function fromWei(value, unit='ether') {
    return Web3.utils.fromWei(value, unit)
}

function toWei(value, unit='ether') {
    return Web3.utils.toWei(value, unit)
}

function App() {

    const web3 = new Web3(window.ethereum)

    const usdt = new web3.eth.Contract(abis.erc20, '0xe963772A9c5aF0e08B3D3F208E151ec734F7bBFa')
    const weth = new web3.eth.Contract(abis.erc20, '0x2bA6d00C8C391e4CF813be313242d5FAad8E8497')
    const susi = new web3.eth.Contract(abis.erc20, '0xC3a26b842Fa8847e11a653b052665D0AbFd98958')

    const ltoken = new web3.eth.Contract(abis.ltoken, '0xd7CfBA0e6B974896330234190fefC86a1bBfA7f1')
    const ptoken = new web3.eth.Contract(abis.ptoken, '0x24210318bd3c9b78524C42798397A5189179842a')
    const router = new web3.eth.Contract(abis.router, '0x1Ca2C2e67b5492B810be48AEB8D85Df779f80350')
    const pool = new web3.eth.Contract(abis.pool, '0x9865Adc01FcA69A057c50e3c6241dB996448E85b')

    const [data, setData] = useState({
        account: '',
        poolinfo: {},
        symbol0: {},
        symbol1: {},
        btoken0: {},
        btoken1: {},
        btoken2: {},
    })

    useEffect(() => {
        update()
    }, [])

    const update = async () => {
        const account = await getAccount()
        const poolinfo = await getPool()
        const symbol0 = await getSymbol(account, 0)
        const symbol1 = await getSymbol(account, 1)
        const btoken0 = await getBToken(account, 0)
        const btoken1 = await getBToken(account, 1)
        const btoken2 = await getBToken(account, 2)
        setData({account, poolinfo, symbol0, symbol1, btoken0, btoken1, btoken2})
    }

    const getAccount = async () => {
        return Web3.utils.toChecksumAddress((await window.ethereum.request({method: 'eth_requestAccounts'}))[0])
    }

    const getPool = async () => {
        return {
            balanceUSDT: fromWei(await usdt.methods.balanceOf(pool.options.address).call(), 'mwei'),
            balanceWETH: fromWei(await weth.methods.balanceOf(pool.options.address).call()),
            balanceSUSI: fromWei(await susi.methods.balanceOf(pool.options.address).call()),
            protocolFeeAccrued: fromWei(await pool.methods.getProtocolFeeAccrued().call()),
        }
    }

    const getSymbol = async (address, symbolId) => {
        const s = await pool.methods.getSymbol(symbolId).call()
        const p = await ptoken.methods.getPosition(address, symbolId).call()
        return {
            symbol: s.symbol,
            price: fromWei(s.price),
            cumulativeFundingRate: fromWei(s.cumulativeFundingRate),
            tradersNetVolume: fromWei(s.tradersNetVolume),
            tradersNetCost: fromWei(s.tradersNetCost),
            user_volume: fromWei(p.volume),
            user_cost: fromWei(p.cost),
            user_lastCumulativeFundingRate: fromWei(p.lastCumulativeFundingRate),
        }
    }

    const getBTokenFromId = (bTokenId) => {
        if (bTokenId === 0) return usdt
        else if (bTokenId === 1) return weth
        else return susi
    }

    const getBToken = async (address, bTokenId) => {
        const b = await pool.methods.getBToken(bTokenId).call()
        const l = await ltoken.methods.getAsset(address, bTokenId).call()
        return {
            symbol: bTokenId ==0 ? 'USDT' : (bTokenId === 1 ? 'WETH' : 'SUSHI'),
            price: fromWei(b.price),
            liquidity: fromWei(b.liquidity),
            pnl: fromWei(b.pnl),
            cumulativePnl: fromWei(b.cumulativePnl),
            user_balance: fromWei(await getBTokenFromId(bTokenId).methods.balanceOf(address).call(), bTokenId === 0 ? 'mwei' : 'ether'),
            user_liquidity: fromWei(l.liquidity),
            user_pnl: fromWei(l.pnl),
            user_lastCumulativePnl: fromWei(l.lastCumulativePnl),
            user_margin: fromWei(await ptoken.methods.getMargin(address, bTokenId).call()),
        }
    }

    const onMint = async (id) => {
        if (id === 0) {
            await usdt.methods.mint(data.account, '10000000000').send({from: data.account})
        } else if (id === 1) {
            await weth.methods.mint(data.account, '10000000000000000000').send({from: data.account})
        } else {
            await susi.methods.mint(data.account, '1000000000000000000000').send({from: data.account})
        }
        await update()
    }

    const onApprove = async (id) => {
        const max = '0xffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff'
        if (id === 0) {
            await usdt.methods.approve(pool.options.address, max).send({from: data.account})
        } else if (id === 1) {
            await weth.methods.approve(pool.options.address, max).send({from: data.account})
        } else {
            await susi.methods.approve(pool.options.address, max).send({from: data.account})
        }
    }

    const onAddLiquidity = async (id, value) => {
        await router.methods.addLiquidity(id, toWei(value)).send({from: data.account})
        await update()
    }

    const onRemoveLiquidity = async (id, value) => {
        await router.methods.removeLiquidity(id, toWei(value)).send({from: data.account})
        await update()
    }

    const onAddMargin = async (id, value) => {
        await router.methods.addMargin(id, toWei(value)).send({from: data.account})
        await update()
    }

    const onRemoveMargin = async (id, value) => {
        await router.methods.removeMargin(id, toWei(value)).send({from: data.account})
        await update()
    }

    const onTrade = async (id, value) => {
        await router.methods.trade(id, toWei(value)).send({from: data.account})
        await update()
    }

    const onLiquidate = async (address) => {
        await router.methods.liquidate(address).send({from: data.account})
        await update()
    }

    return (
        <div className="App">
            <h1>Deri V2</h1>
            <Pool poolinfo={data.poolinfo} b0={data.btoken0} b1={data.btoken1} b2={data.btoken2} onMint={onMint} onApprove={onApprove}
                  onAddLiquidity={onAddLiquidity} onRemoveLiquidity={onRemoveLiquidity} onAddMargin={onAddMargin} onRemoveMargin={onRemoveMargin}
                  onTrade={onTrade} onLiquidate ={onLiquidate} />
            <Symbols s0={data.symbol0} s1={data.symbol1} onTrade={onTrade} />
            <BTokens b0={data.btoken0} b1={data.btoken1} b2={data.btoken2} />
            <Liquidation onLiquidate={onLiquidate} />
        </div>
    )

}

export default App

